*, :before, :after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  tab-size: 4;
  font-family: system-ui, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
  line-height: 1.15;
}

body {
  margin: 0;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --text: #131313;
  --main-green: #84a178;
  --card-green: #aec6a5;
  --ingredients-green: #9cbe8f;
  --button-green: #0a8817;
  --grey-disabled: #ababab;
  --yellow: #f8d24e;
  --card-yellow: #efd478;
  --form-yellow-underlay: #f8d24e1a;
  --brown: #d6936d;
  --brown-underlay: #d5753fb2;
  --green-underlay: #9cbe8fb2;
  --yellow-underlay: #f8d24eb2;
  --text-white: #fcfcfc;
  --stroke: #e1e1e1;
  --white: #fff;
  --black: #000;
  --box-shadow: 0px 8px 30px 0px #d414431a;
  --transition-general-duration: .25s;
  --transition-general-function: cubic-bezier(.4, 0, .2, 1);
  --transition-general: var(--transition-general-duration) var(--transition-general-function);
}

body {
  color: var(--text);
  background-color: var(--main-green);
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  border: none;
  margin: 0;
  padding: 0;
}

.btn {
  color: var(--text);
  cursor: pointer;
  border-radius: 10px;
  outline: none;
  font-size: 14px;
  font-weight: 600;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

svg {
  fill: currentColor;
  display: block;
}

.container {
  max-width: 480px;
  margin: 0 auto;
  padding: 0 20px;
}

.section {
  margin-top: 60px;
  margin-bottom: 60px;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  white-space: nowrap;
  width: 1px;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 2000;
  background-color: #0a0a0a80;
  padding: 20px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.gallery-customers-review-section {
  background-color: var(--text-white);
  width: 100%;
  height: 1110px;
  background-image: url("background-desktop.76841d46.png");
