.gallery-list {
  overflow: hidden;
}

.gallery-item {
  max-width: 440px;
  margin: 0 auto;
  animation-name: gallery;
  animation-duration: 9s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  position: absolute;
  top: -49px;
  left: 20px;
  right: 20px;
}

.gallery-item:nth-of-type(1) {
  animation-delay: 6s;
}

.gallery-item:nth-of-type(2) {
